import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import RolesTable from './Tables/RolesTable';
import RolesModal from './Modals/RolesModal'
import SubMenuSearch from '../../SearchComponents/SubMenuSearch/SubMenuSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'

class Roles extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ROLE_ID: '',
            ROLE_NAME: '',
            SUB_MENU_ID: '',
            SUB_MENU_NAME: '',
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: '',
            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        
    }

    populateData() {
        const getdata = `
            query{
                Roles{
                    ROLE_ID
                    ROLE_NAME
                    SUB_MENU_ID
                    SUB_MENU {
                        SUB_MENU_ID
                        SUB_MENU_NAME
                    }
                    CREATEDBYID
                    LASTMODIFIEDBYID
                    DATECREATED
                    DATELASTMODIFIED
                }

                SubMenus {
                    SUB_MENU_ID
                    MAIN_MENU_ID
                    SUB_MENU_NAME
                    MENU_PATH
                    MAIN_MENU_NAME
                }
            }
        `;
    
        API.getData(getdata)
        .then(({ data }) => this.setState({
            data: data.data.Roles,
            searchdata: data.data.SubMenus
        })).catch(error => {
            if (error.response || error.request) {
                toastr.error('Error Loading Data', 'Failed to Load Data');
            }
        })

    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
            mutation{
                addRole(
                    ROLE_NAME:"${this.state.ROLE_NAME}",
                    SUB_MENU_ID:${this.state.SUB_MENU_ID},
                    ROLE_DESCRIPTION:"${this.state.ROLE_NAME}",
                    RECORD_STATUS:1,
                    CREATEDBYID:${this.state.CREATEDBYID},
                    LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                    DATECREATED:"${this.state.DATECREATED}",
                    DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                    ROLE_ID
                }
            }
        `;

        const formEdit = `
            mutation{
                updateRole(
                    ROLE_ID:${this.state.ROLE_ID},
                    ROLE_NAME:"${this.state.ROLE_NAME}",
                    SUB_MENU_ID:${this.state.SUB_MENU_ID},
                    ROLE_DESCRIPTION:"${this.state.ROLE_NAME}",
                    RECORD_STATUS:1,
                    LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                    DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                    ROLE_ID
                }
            }
        `;

        

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then((resp) => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then((resp) => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
            mutation{
                deleteRole(
                    ROLE_ID:${this.state.ROLE_ID}
                    ){
                    ROLE_ID
                }
            }
        `;
        
        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleEditData = (e) => {
        this.setState({
            ROLE_ID: e.ROLE_ID,
            ROLE_NAME: e.ROLE_NAME,
            SUB_MENU_ID: e.SUB_MENU_ID,
            SUB_MENU_NAME: e.SUB_MENU.SUB_MENU_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ROLE_ID: e.ROLE_ID,
            ROLE_NAME: e.ROLE_NAME,
            // SUB_MENU_ID: e.SUB_MENU.SUB_MENU_ID,
            // SUB_MENU_NAME: e.SUB_MENU.SUB_MENU_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            ROLE_ID: '',
            ROLE_NAME: '',
            SUB_MENU_ID: '',
            SUB_MENU_NAME: '',
        })
    }

    handleCloseSearch = (e) => {
        this.setState({
            SUB_MENU_ID: e.SUB_MENU_ID,
            SUB_MENU_NAME: e.SUB_MENU_NAME,
            opensearch: false,
        });
    };

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

            if (value.length) {
                filteredData = data.filter(item => {
                    let startsWithCondition =
                        item.ROLE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())||
                        item.SUB_MENU.SUB_MENU_NAME.toString().toLowerCase().startsWith(value.toLowerCase())
    
                    let includesCondition =
                        item.ROLE_NAME.toString().toLowerCase().includes(value.toLowerCase())||
                        item.SUB_MENU.SUB_MENU_NAME.toString().toLowerCase().includes(value.toLowerCase())
    
                    if (startsWithCondition) {
                        return startsWithCondition
                    } else if (!startsWithCondition && includesCondition) {
                        return includesCondition
                    } else return null
                })
                this.setState({ filteredData })
            }

    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let searchFilteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            searchFilteredData = data.filter(item => {
                let startsWithCondition =
                    item.SUB_MENU_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.MAIN_MENU_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SUB_MENU_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.MAIN_MENU_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ searchFilteredData })
        }
    }

    render() {
        const { data, columns, value, filteredData, searchdata, searchvalue, searchFilteredData } = this.state

        const isEnabled = !(this.state.SUB_MENU_ID !== '' && this.state.ROLE_NAME !== '');
        
        return (
            <>
                <Helmet><title>Roles</title></Helmet>

                <React.Fragment>
                <Breadcrumbs
                    breadCrumbTitle="Settings"
                    breadCrumbParent="Home"
                    breadCrumbActive="Roles"
                />
                <Row>
                    <Col sm="12">
                        <Card>
                            <CardHeader>
                                <CardTitle>Roles</CardTitle>
                            </CardHeader>
                            <CardBody className="rdt_Wrapper">
                                
                                <RolesTable
                                    role="Roles"
                                    handleEdit={this.handleEditData}
                                    handleDelete={this.handleDeleteData}
                                    value={value}
                                    data={data}
                                    columns={columns}
                                    filteredData={filteredData}
                                    handleShow={this.handleShow}
                                    handleFilter={this.handleFilter}
                                />

                                <RolesModal
                                    show={this.state.show}
                                    handleHide={this.handleHide}
                                    InsertUpdate={this.state.InsertUpdate}
                                    handleSubmit={this.handleSubmit}
                                    handleShowSearch={this.handleShowSearch}
                                    isEnabled={isEnabled}

                                    role_id={this.state.ROLE_ID}
                                    role_name={this.state.ROLE_NAME}
                                    sub_menu_id={this.state.SUB_MENU_ID}
                                    sub_menu_name={this.state.SUB_MENU_NAME}

                                    handleSubMenuName={e => this.setState({SUB_MENU_NAME: e.target.value })}
                                    changeRoleName={e => this.setState({ROLE_NAME: e.target.value })}

                                    showdelete={this.state.showdelete}
                                    handleHideDelete={this.handleHideDelete}
                                    handleDelete={this.handleDelete}
                                />

                                <SubMenuSearch
                                    handleSearchSubMenu={this.handleCloseSearch}
                                    opensearch={this.state.opensearch}
                                    handleHideSearch={this.handleHideSearch}
                                    value={searchvalue}
                                    data={searchdata}
                                    handleFilter={this.handleSearchFilter}
                                    filteredData={searchFilteredData}
                                />

                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </React.Fragment>
        
            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(Roles));