import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import { Search } from 'react-feather';

class RolesModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Role
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>

                            <Form.Group controlId="sub_menu_name">
                                <Form.Label>Sub Menu</Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        value={this.props.sub_menu_name}
                                        onChange={this.props.handleSubMenuName}
                                        placeholder="Search Sub Menu"
                                        aria-describedby="inputGroupPrepend"
                                        required
                                        readOnly
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                            </Form.Group>
                            
                            <Form.Group controlId="role_name">
                                <Form.Label>Role</Form.Label>
                                <Form.Control type="text" placeholder="Enter Role Name" value={this.props.role_name} onChange={this.props.changeRoleName} required/>
                            </Form.Group>
                            
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Role
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="role_name_del">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.role_name} onChange={this.props.changeDescription} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default RolesModal;